/*! purgecss start ignore */
@import "aos/src/sass/aos.scss"; /*! purgecss end ignore */
@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500;600;700;800;900;1000&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

// Mixins
@mixin flexCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}
@mixin flexBox($justify, $align) {
  display: flex;
  justify-content: $justify;
  align-items: $align;
}

// Colors
$main-color: #66c3ff;
$main-color-alt: #d7bce8;
$accent-color: #121217;
$accent-color-alt: #0b2d50;
$background-color: #0d0d11;
$text-color: #ffffff;
$white-color: #ffffff;

// UI
$main-transition: 0.4s;
$main-line-height: 1.4;

// Fonts
$headings-font: "Cairo", sans-serif;
$paragraph-font: "Raleway", sans-serif;

// Global Rules Start

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  user-select: none;
}

body {
  font-family: $paragraph-font;
  background-color: $background-color;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 10px;
  transition: $main-transition;
  background-color: $accent-color;
}

::-webkit-scrollbar-thumb {
  background-color: $main-color-alt;
}

.calendly-inline-widget {
  height: 100vh !important;
}

@media screen and (max-width: 769px) {
  .calendly-inline-widget {
    height: 80vh !important;
  }
}

// Animations
@keyframes slide-down {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}

@keyframes text-gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
